.page {
  font-family: Poppins, sans-serif;
  color: $main-text-color;

  scroll-behavior: smooth;

  &__favicon {
    width: 100px;
    height: auto;
    display: block;
    margin: 0 auto;
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

  &:has(.page__menu:target) {
    overflow: hidden;
  }
}
